import './App.css';
import Navbar from './components/Navbar';
import TextForm from './components/TextForm'
// import About from './components/About';
import Alert from './components/Alert';
import {useState} from 'react';
// import {BrowserRouter as Router, Switch, Route} from 'react-router-dom'


function App() {
  const [mode, setMode] = useState('light');
  const [alert,setAlert]= useState(null);

  const showAlert =(message,type)=>{
    setAlert({
      message:message,
      type:type
    })
    setTimeout(()=>{
      setAlert(null);
    },2000)
  }

  const toggleMode =()=>{
    if(mode==='light'){
      setMode('dark');
      document.body.style.backgroundColor='#042743';
      // document.body.style.color='white';
      showAlert("Dark Mode has been enabled","success");
      // document.title="TextUtils-DarkMode";
    }else{
      setMode('light');
      document.body.style.backgroundColor='white';
      // document.body.style.color='black';
      showAlert("Light Mode has been enabled","success");
      // document.title="TextUtils-LightMode";
    }
  }
  return (
    <>
    {/* <Router> */}
      <Navbar title="TextUtils" aboutText="About TextUtils" mode={mode} toggleMode={toggleMode}></Navbar>
      <Alert alert={alert}></Alert>
      {/* <Switch> */}
        {/* <Route exact path="/"> */}
      <div className="container my-3">
      <TextForm showAlert={showAlert} heading="Try TextUtils- Word Counter, Character Counter, Remove Extra Spaces" mode={mode}></TextForm>
      </div>
        {/* </Route> */}
        {/* <Route exact path="/about"> */}
      {/* <About mode={mode}></About> */}
        {/* </Route> */}
      {/* </Switch> */}
    {/* </Router> */}
    </>
  );
}

export default App;
