import React,{useState} from 'react'

const TextForm = (props) => {
    const [text,setText]=useState('');
    // const [word,setWordLength]=useState('');

    const handleOnChange =(e)=>{
        setText(e.target.value);
    }

    //Function to change to uppercase
    const handleUpClick =()=>{
        let uppercase=text.toUpperCase();
        setText(uppercase);
        props.showAlert("Converted to uppercase","success");
    }
    
    //Function to change to lowercase
    const handleLowerClick =()=>{
        let lowercase=text.toLowerCase();
        setText(lowercase);
        props.showAlert("Converted to lowercase","success");
    }
    
    //Function to clear all data
    const handleClearText =()=>{
        setText('');
        props.showAlert("Cleared all data","success");
    }
    
    //Function to copy all data
    const handleCopy =()=>{
        let text=document.getElementById("myBox");
        // text.select();
        navigator.clipboard.writeText(text.value);
        // document.getSelection().removeAllRanges();
        props.showAlert("Copied all data","success");
    }
    
    //Function toremove extra spaces
    const handleExtraSpaces =()=>{
        let newText=text.split(/[ ]+/);
        setText(newText.join(" "));
        props.showAlert("Removed Extra Spaces","success");
    }
    
    // const countword =()=>{
        //     if(text=''){
            //         setWordLength(0);
            //     }else{
                //         let wrd=text.split(" ").length;
    //         setWordLength(wrd);
    //     }
    // }

    return (
        <><div className="container">
            <h1 className="mb-4" style={{color:(props.mode==='light')?'black':'white'}}>{props.heading}</h1>
            <div className="mb-3">
                <textarea className="form-control" id="myBox" rows="8" cols="4" onChange={handleOnChange} value={text}
                style={{backgroundColor:(props.mode==='light')?'#f4e7e7':'#13466e',color:(props.mode==='light')?'black':'white'}}></textarea>
            </div>
            <button className="btn btn-primary me-3 my-1 " onClick={handleUpClick} disabled={text.length===0}>Convert To UPPERCASE</button>
            <button className="btn btn-primary me-3 my-1" onClick={handleLowerClick} disabled={text.length===0}>Convert To lowercase</button>
            <button className="btn btn-primary me-3 my-1" onClick={handleClearText} disabled={text.length===0}>Clear Text</button>
            <button className="btn btn-primary me-3 my-1" onClick={handleCopy} disabled={text.length===0}>Copy Text</button>
            <button className="btn btn-primary me-3 my-1" onClick={handleExtraSpaces} disabled={text.length===0}>Remove Extra Spaces</button>
        </div>

        <div className="container my-3" style={{color:(props.mode==='light')?'black':'white'}}>
            <h2>Your Text Summarry...</h2>
            <p>{text.split(/\s+/).filter((elem)=>{return elem.length!==0}).length} words, {text.length} characters</p>
            <p>You can read above para in {0.008*text.split(" ").length} Minutes...</p>
            <h2>Your Para Preview...</h2>
            <p>{text.length>0? text:"Nothing to preview ! Enter text above ðŸ†™ "}</p>
        </div>
        </>
    )
}

export default TextForm
